<template>
  <section class="theSection3">
    <div class="theContainer">
      <header class="theSection3__header">
        <h1 class="theSection3__title">Sebrae pelo Brasil</h1>
      </header>
      <div class="theSection3__content">
        <div class="theSection3__map">
          <div class="theMap">
            <img src="@/assets/images/mapa.png" alt="Sebrae pelo Brasil" />
          </div>
        </div>

        <div class="theSection3__tables">
          <div class="theTables">
            <TableStates :dataTable="Table1" />
            <TableStates :dataTable="Table2" />
            <TableStates :dataTable="Table3" />
            <TableStates :dataTable="Table4" />
          </div>

          <div class="theTableslegend">
            <header class="theTableslegend__header">
              <h2 class="theTableslegend__title">Legenda</h2>
            </header>
            <div class="theTableslegend__content">
              <div
                class="theTableslegend__box openModal"
                @click="handleClick('agencia')"
              >
                Agência
                <br />
                de Atendimento
                <div class="btnmais">
                  <img src="@/assets/images/btnmais.png" alt />
                </div>
              </div>
              <div
                class="theTableslegend__box openModal"
                @click="handleClick('lab')"
              >
                SebraeLab
                <div class="btnmais">
                  <img src="@/assets/images/btnmais.png" alt />
                </div>
              </div>
              <div
                class="theTableslegend__box openModal"
                @click="handleClick('espaco')"
              >
                Espaço Sebrae
                <br />
                em parceiros
                <div class="btnmais">
                  <img src="@/assets/images/btnmais.png" alt />
                </div>
              </div>
              <div
                class="theTableslegend__box openModal"
                @click="handleClick('sala')"
              >
                Sala do
                <br />
                empreendedor
                <div class="btnmais">
                  <img src="@/assets/images/btnmais.png" alt />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import { mapState } from "vuex";
import TableStates from "@/components/TableStates/TableStates";
import Table1 from "@/TheSections/TheSection3_Data/table1.js";
import Table2 from "@/TheSections/TheSection3_Data/table2.js";
import Table3 from "@/TheSections/TheSection3_Data/table3.js";
import Table4 from "@/TheSections/TheSection3_Data/table4.js";
import { TweenMax, TimelineMax } from "gsap/all";

export default {
  name: "TheSection3",
  data() {
    return {
      Table1: Table1,
      Table2: Table2,
      Table3: Table3,
      Table4: Table4
    };
  },
  components: {
    TableStates
  },
  computed: {
    ...mapState({
      modal: state => state.modal
    })
  },
  methods: {
    handleClick(valor) {
      this.$store.commit("changeModal", valor);
    },
    set() {
      const theMap = document.querySelector(".theMap");
      const title = document.querySelector(".theSection3__header");
      const text = document.querySelector(".theSection2__text");
      const tables = document.querySelectorAll(".TableStates");

      TweenMax.set(theMap, {
        opacity: 0,
        y: 100
      });

      TweenMax.set(title, {
        opacity: 0
      });

      TweenMax.set(text, {
        opacity: 0,
        y: 100
      });

      TweenMax.set(tables, {
        opacity: 0,
        y: 100
      });
    },
    anime() {
      const theMap = document.querySelector(".theMap");
      const title = document.querySelector(".theSection3__header");
      const text = document.querySelector(".theSection2__text");
      const tables = document.querySelectorAll(".TableStates");
      const tl = new TimelineMax();

      tl.to(theMap, 1, {
        opacity: 1,
        y: 0
      })
        .to(title, 1, {
          opacity: 1
        })
        .to(
          text,
          1,
          {
            opacity: 1,
            y: 0
          },
          "-=1"
        );

      TweenMax.staggerTo(
        tables,
        1,
        {
          opacity: 1,
          y: 0
        },
        1
      );
    },
    scrollSpy() {
      const section = document.querySelector(".theSection3");

      window.addEventListener("scroll", () => {
        if (section.classList.contains("sectionActive")) {
          this.count += 1;
          if (this.count < 2) {
            this.anime();
          }
        } else {
          this.count = 0;
        }
      });
    }
  },
  mounted() {
    //this.scrollSpy();
    //this.set();
  }
};
</script>
