<template>
  <section class="theSection4">
    <div class="theContainer--full">
      <div class="theSection4__slider">
        <slick ref="slick" :options="slickOptions" class="theSlider">
          <div
            class="theSlider__slide"
            :class="slide.id"
            v-for="slide in slides"
            :key="slide.id"
            :id="slide.id"
          >
            <div class="theSlider__figure">
              <img
                :src="require(`@/assets/images/${slide.image}`)"
                alt="Números Sebrae 2019"
              />
            </div>

            <div class="theSlider__content">
              <div class="theSlider__title">{{ slide.title }}</div>
              <div class="theSlider__numbers">
                <NumbersGroup
                  v-for="item in slide.numbers"
                  :key="item.number"
                  :number="item.number"
                  :title1="item.title1"
                  :title2="item.title2"
                />
              </div>
            </div>
          </div>
        </slick>
      </div>
      <div class="theSlider__arrows">
        <span class="theSlider__arrows__prev">
          <img src="@/assets/images/setaE.png" alt />
        </span>
        <span class="theSlider__arrows__next">
          <img src="@/assets/images/setaD.png" alt />
        </span>
      </div>
    </div>
  </section>
</template>

<script>
import Slick from "vue-slick";
import NumbersGroup from "@/components/NumbersGroup/NumbersGroup";
import Slide1 from "@/TheSections/TheSection4_Data/slide1.js";
import Slide2 from "@/TheSections/TheSection4_Data/slide2.js";
import Slide3 from "@/TheSections/TheSection4_Data/slide3.js";

export default {
  name: "TheSection4",
  components: { Slick, NumbersGroup },
  data() {
    return {
      slickOptions: {
        slideToShow: 1,
        autoplay: false,
        autoplaySpeed: 6000,
        fade: true
      },
      slides: [Slide1, Slide2, Slide3]
    };
  },
  methods: {
    includeArrows() {
      const arrowPrev = document.querySelector(".theSlider__arrows__prev");
      const arrowNext = document.querySelector(".theSlider__arrows__next");
      const slickPrev = document.querySelector(".slick-prev");
      const slickNext = document.querySelector(".slick-next");

      slickPrev.appendChild(arrowPrev);
      slickNext.appendChild(arrowNext);
    }
  },
  mounted() {
    this.includeArrows();
  }
};
</script>
