import Vue from "vue";
import Router from "vue-router";
import Home from "./views/Home.vue";

Vue.use(Router);

const goHash = e => {
  if (e == "#introducao") {
    window.scrollTo({
      top: document.querySelector(e).offsetTop - 260,
      behavior: "smooth"
    });
  } else {
    window.scrollTo({
      top: document.querySelector(e).offsetTop,
      behavior: "smooth"
    });
  }
};

export default new Router({
  //mode: "history",
  //base: process.env.BASE_URL,
  scrollBehavior(to) {
    if (to.hash) {
      return goHash(to.hash);
    }

    return goHash(to.hash);
  },
  routes: [
    {
      path: "/",
      name: "Inicio",
      component: Home
    }
  ]
});
