<template>
  <div class="home">
    <TheSection1 id="home" class="theSection" />
    <TheSection2 id="introducao" class="theSection" />
    <TheSection3 id="sebraepelobrasil" class="theSection" />
    <TheSection4 id="numeros" class="theSection" />
    <ModalSection />
  </div>
</template>

<script>
import TheSection1 from "@/TheSections/TheSection1";
import TheSection2 from "@/TheSections/TheSection2";
import TheSection3 from "@/TheSections/TheSection3";
import TheSection4 from "@/TheSections/TheSection4";
import ModalSection from "@/TheSections/ModalSection";

export default {
  name: "Home",
  data() {
    return {};
  },
  components: {
    TheSection1,
    TheSection2,
    TheSection3,
    TheSection4,
    ModalSection
  },
  methods: {
    scrollSpy() {
      const theSections = document.querySelectorAll(".theSection");
      let sectionID;
      let currentSection;
      let sectionTop;
      let sectionbottom;
      let inViewSection;

      const activeSection = () => {
        history.pushState(null, "teste", "#" + inViewSection);
      };

      const checkInView = () => {
        theSections.forEach(section => {
          sectionTop = section.getBoundingClientRect().top;
          sectionbottom = section.getBoundingClientRect().bottom;

          if (sectionTop < 80 && sectionbottom > 200) {
            sectionID = section.getAttribute("id");
            if (currentSection != sectionID) {
              currentSection = sectionID;
              inViewSection = sectionID;
              activeSection();
            }
          }
        });
      };

      window.addEventListener("wheel", checkInView);
    }
  },
  mounted() {
    this.scrollSpy();
  }
};
</script>
