<template>
  <section class="theSection2">
    <div class="theContainer">
      <header class="theSection2__header">
        <div class="theSection2__video">
          <div class="theVideo">
            <iframe
              width="560"
              height="315"
              src="https://www.youtube.com/embed/G2fmYkYezJU"
              frameborder="0"
              allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
              allowfullscreen
            ></iframe>
          </div>
        </div>
      </header>

      <div class="theSection2__content">
        <div class="theSection2__text">
          <p>
            Eles são pequenos, mas suas conquistas são gigantes. A força que
            move o país vem das mais de 15 milhões de micro e pequenas empresas.
            Elas representam 27% do PIB brasileiro e mais da metade dos empregos
            formais. Esses números são representativos, mas, para o Sebrae, os
            pequenos negócios são muito mais que números. Por trás de cada
            negócio há sonhos, projetos de vida e o esforço de quem trabalha
            duro para transformar a realidade e a sua comunidade. O Sebrae atua
            na capacitação, na orientação para a melhoria da gestão e na criação
            de um ambiente legal favorável ao crescimento dos pequenos negócios.
            O resultado é um Brasil mais forte, no qual as micro e pequenas
            empresas, que representam 99% dos negócios do país, geram emprego e
            renda.
          </p>
          <p>
            <span class="featured"
              >Sebrae. A força do empreendedor brasileiro.</span
            >
          </p>
        </div>
      </div>
    </div>

    <div class="techs">
      <TechDots />
      <TechLine class="techLine__first" />
      <TechLine class="techLine__last" />
    </div>
    <div class="modal"></div>
  </section>
</template>

<script>
import TechLine from "@/components/TechLine/TechLine";
import TechDots from "@/components/TechDots/TechDots";
import { TweenMax, TimelineMax } from "gsap/all";

export default {
  name: "TheSection2",
  data() {
    return {
      isActive: false,
      count: 0
    };
  },
  components: {
    TechLine,
    TechDots
  },
  methods: {
    set() {
      const theVideo = document.querySelector(".theSection2__video");
      const dots = document.querySelector(".techDots");
      const text = document.querySelector(".theSection2__text");

      TweenMax.set(theVideo, {
        opacity: 0,
        y: 100
      });

      TweenMax.set(dots, {
        opacity: 0
      });

      TweenMax.set(text, {
        opacity: 0,
        y: 100
      });
    },
    anime() {
      const theVideo = document.querySelector(".theSection2__video");
      const dots = document.querySelector(".techDots");
      const text = document.querySelector(".theSection2__text");
      const tl = new TimelineMax();

      tl.to(theVideo, 1, {
        opacity: 1,
        y: 0
      })
        .to(
          dots,
          1,
          {
            opacity: 1
          },
          "-=0.5"
        )
        .to(
          text,
          1,
          {
            opacity: 1,
            y: 0
          },
          "-=1"
        );
    },
    scrollSpy() {
      const section = document.querySelector(".theSection2");

      window.addEventListener("scroll", () => {
        if (section.classList.contains("sectionActive")) {
          this.count += 1;
          if (this.count < 2) {
            this.anime();
          }
        } else {
          this.count = 0;
        }
      });
    }
  },
  mounted() {
    //this.scrollSpy();
    //this.set();
  }
};
</script>
