<template>
  <nav class="slidingTopMenu">
    <div class="slidingTopMenu__icon" @click="navAnimate()">
      <span></span>
      <span></span>
      <span></span>
    </div>
    <ul class="slidingTopMenu__menu">
      <li
        class="slidingTopMenu__item"
        v-for="(item, index) in items"
        :key="item + index"
        @click="navAnimate()"
      >
        <router-link
          class="slidingTopMenu__link"
          :to="item.link"
          :title="item.name"
          v-html="item.name"
        ></router-link>
      </li>
    </ul>
  </nav>
</template>

<script>
import { TweenMax } from "gsap/all";

export default {
  name: "SlidingTopMenu",
  data() {
    return {
      navActive: false
    };
  },
  props: {
    items: {
      default() {
        return [
          {
            name: "link 1",
            link: "#link1"
          }
        ];
      }
    }
  },
  methods: {
    activeBody() {
      const body = document.body;
      body.classList.toggle("activeBody");
    },
    navSet() {
      const navItems = document.querySelectorAll(".slidingTopMenu__item");

      TweenMax.set(navItems, {
        x: -30,
        opacity: 0
      });
    },
    navAnimate() {
      this.activeBody();
      const navItems = document.querySelectorAll(".slidingTopMenu__item");

      if (!this.navActive) {
        this.navActive = true;

        TweenMax.staggerTo(
          navItems,
          0.5,
          {
            x: 0,
            opacity: 1
          },
          0.1
        );
      } else {
        this.navActive = false;

        TweenMax.staggerTo(
          navItems,
          0.5,
          {
            x: -30,
            opacity: 0
          },
          0.1
        );
      }
    }
  },
  mounted() {
    this.navSet();
  }
};
</script>

<style lang="sass">
@import '../../assets/styles/settings/vars'
@import '../../assets/styles/settings/mixins'
@import '../../assets/styles/settings/mediaQueries'
@import './styles/SlidingTopMenu'
