<template>
  <div class="theModal">
    <div class="theModal__overlay" @click="closeModal()"></div>
    <div class="theModal__container">
      <div class="theModal__box">
        <header class="theModal__header">
          <h1 class="theModal__title" v-html="title"></h1>
          <div class="theModal__close" @click="closeModal()">
            <img src="@/assets/images/btnfechar.png" alt />
          </div>
        </header>

        <div class="theModal__content">
          <slot>modal text</slot>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { TweenMax } from "gsap/all";
export default {
  name: "TheModal",
  props: {
    title: {
      default: "title modal"
    }
  },
  computed: {
    ...mapState({
      modal: state => state.modal
    })
  },
  methods: {
    openModal() {
      const html = document.body.parentNode;
      const buttonOpen = document.querySelectorAll(".openModal");
      const modal = document.querySelector(".theModal");
      const modalContainer = document.querySelector(".theModal__container");
      buttonOpen.forEach(button => {
        button.addEventListener("click", fadeInScale);
      });
      function fadeInScale() {
        html.classList.add("modalOn");
        TweenMax.set(modal, { opacity: 0 });
        TweenMax.set(modalContainer, { opacity: 0, y: 100 });
        TweenMax.to(modal, 0.5, {
          display: "block"
        });
        TweenMax.to(modal, 0.5, {
          autoAlpha: 1
        });
        TweenMax.to(modalContainer, 0.5, {
          autoAlpha: 1,
          y: 0,
          delay: 0.5
        });
      }
    },
    closeModal() {
      const html = document.body.parentNode;
      const modal = document.querySelector(".theModal");
      const modalContainer = document.querySelector(".theModal__container");
      html.classList.remove("modalOn");
      TweenMax.to(modalContainer, 0.5, {
        autoAlpha: 0,
        y: 50
      });
      TweenMax.to(modal, 0.5, {
        autoAlpha: 0,
        delay: 0.5
      });
      TweenMax.to(modal, 0.5, {
        display: "none"
      });
    }
  },
  mounted() {
    this.openModal();
  }
};
</script>
