<template>
  <header class="theHeader">
    <div class="theContainer">
      <div class="theHeader__menu">
        <SlidingTopMenu :items="items" />
      </div>
      <div class="theHeader__brand">
        <RouterLinkImage link="#home" target="_self">
          <slot>
            <SebraeSymbol />
          </slot>
        </RouterLinkImage>
      </div>
    </div>
  </header>
</template>

<script>
import SlidingTopMenu from "@/components/SlidingTopMenu/SlidingTopMenu";
import RouterLinkImage from "@/components/RouterLinkImage/RouterLinkImage";
import SebraeSymbol from "@/assets/images/marca.svg";
import { TweenMax } from "gsap/all";

export default {
  name: "TheHeader",
  data() {
    return {
      items: [
        {
          name: "Introdução",
          link: "#introducao"
        },
        {
          name: "Sebrae pelo Brasil",
          link: "#sebraepelobrasil"
        },
        {
          name: "Números",
          link: "#numeros"
        }
      ]
    };
  },
  components: {
    SlidingTopMenu,
    RouterLinkImage,
    SebraeSymbol
  },
  methods: {
    fixNav() {
      const theHeader = document.querySelector(".theHeader");
      const sticky = theHeader.offsetTop + 72;

      const handleScroll = () => {
        if (window.pageYOffset >= sticky) {
          theHeader.classList.add("fixNav");

          if (window.pageYOffset < 200) {
            TweenMax.set(theHeader, {
              y: -72
            });
          }

          TweenMax.to(theHeader, 1, {
            y: 0
          });
        } else {
          theHeader.classList.remove("fixNav");

          TweenMax.set(theHeader, {
            y: 0
          });
        }
      };

      window.addEventListener("scroll", handleScroll);
    }
  },
  mounted() {
    this.fixNav();
  }
};
</script>
